body {
  overflow: hidden;
}

::-webkit-scrollbar {
  width: 0;  
}

::-webkit-scrollbar-track, ::-webkit-scrollbar-thumb, ::-webkit-scrollbar-corner {
  background: transparent; 
}

#hero-container{
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;

}

svg {
  width: 100%;
  height: 100%;
}


#navbar {
  z-index: 20;
  position: sticky;
  max-height: 100px;
  max-width: 2000px;
  margin: 0 auto;

}

#logo {
  max-height: 100px;
  margin-top: 20px;
  margin-left: 40px;
}